<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";
import echarts from "echarts";

export default {
  data() {
    return {
      dataShadow: [1, 2, 3, 4],
      yMax: 500,
      dataAxis: [
        "百度",
        "新浪",
        "威海",
        "腾讯网",
        "百家号",
        "知乎",
        "大鱼号",
        "头条号",
        "公众号",
        "趣头条"
      ],
      num: [
        220,
        182,
        191,
        234,
        290,
        330,
        310,
        123,
        99,
        108
      ],
    };
  },
  computed: {
    option() {
      return {
        title: {
          text: "关键词热度排行",
          left: "center",
          top: "4%",
          textStyle: {
            //文字的样式
            fontSize: "100%",
            fontWeight: "normal",
            color: "#03D3D3",
          },
        },
        xAxis: {
          data: this.dataAxis,
          axisLabel: {
            interval: 0,
            rotate: 40,
            // inside: true,
            // textStyle: {
            //   color: "#fff",
            // },
          },
          axisTick: {
            show: false,
          },
          axisLine: {
            lineStyle: {
              color: "rgba(246,240,235,1)",
            },
          },
          z: 10,
        },
        yAxis: {
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            textStyle: {
              color: "#999",
            },
          },
        },
        dataZoom: [
          {
            type: "inside",
          },
        ],
        series: [
          {
            // For shadow
            type: "bar",
            itemStyle: {
              color: "rgba(0,0,0,0.05)",
            },
            barGap: "-100%",
            barCategoryGap: "40%",
            data: this.dataShadow,
            animation: false,
          },
          {
            type: "bar",
            itemStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: "#FFC600" },
                { offset: 1, color: "#FFC60000" }
              ]),
            },
            data: this.num,
          },
        ],
      };
    },
  },
  mounted() {
    this.$unlock({ monthSelect: true });
    this.play();
  },
  methods: {
    play() {
      this.dataShadow = this.num.map(() => this.yMax);
    },
  },
  components: {
    Echart,
  },
};
</script>

